import { Transaction, FilterOptions } from '../types/financial';
import { stringify } from 'csv-stringify/sync';

export interface ExportOptions {
  format: 'csv' | 'json';
  includeHeaders: boolean;
  dateFormat: string;
  currencyFormat: string;
}

export const defaultExportOptions: ExportOptions = {
  format: 'csv',
  includeHeaders: true,
  dateFormat: 'MMM dd, yyyy',
  currencyFormat: 'USD',
};

// Filter transactions based on export options
export const filterTransactionsForExport = (
  transactions: Transaction[],
  filters: FilterOptions
): Transaction[] => {
  let filteredTransactions = [...transactions];

  // Apply date range filter
  if (filters.dateRange) {
    filteredTransactions = filteredTransactions.filter(transaction => {
      const transactionDate = new Date(transaction.date);
      const startDate = new Date(filters.dateRange!.start);
      const endDate = new Date(filters.dateRange!.end);
      return transactionDate >= startDate && transactionDate <= endDate;
    });
  }

  // Apply amount range filter
  if (filters.amountRange) {
    filteredTransactions = filteredTransactions.filter(transaction => {
      const amount = Math.abs(transaction.amount);
      return amount >= filters.amountRange!.min && amount <= filters.amountRange!.max;
    });
  }

  // Apply category filter
  if (filters.categories && filters.categories.length > 0) {
    filteredTransactions = filteredTransactions.filter(transaction =>
      filters.categories!.includes(transaction.category)
    );
  }

  // Apply tag filter
  if (filters.tags && filters.tags.length > 0) {
    filteredTransactions = filteredTransactions.filter(transaction =>
      filters.tags!.some(tag => transaction.tags.includes(tag))
    );
  }

  // Apply account filter
  if (filters.accountIds && filters.accountIds.length > 0) {
    filteredTransactions = filteredTransactions.filter(transaction =>
      filters.accountIds!.includes(transaction.accountId)
    );
  }

  // Apply transaction type filter
  if (filters.transactionTypes && filters.transactionTypes.length > 0) {
    filteredTransactions = filteredTransactions.filter(transaction => {
      const isIncome = transaction.amount > 0;
      const isExpense = transaction.amount < 0;
      
      if (filters.transactionTypes!.includes('income') && isIncome) return true;
      if (filters.transactionTypes!.includes('expense') && isExpense) return true;
      return false;
    });
  }

  // Apply status filter
  if (filters.status && filters.status.length > 0) {
    filteredTransactions = filteredTransactions.filter(transaction => {
      const isPending = transaction.pending;
      const isCleared = !transaction.pending;
      
      if (filters.status!.includes('pending') && isPending) return true;
      if (filters.status!.includes('cleared') && isCleared) return true;
      return false;
    });
  }

  // Apply search term filter
  if (filters.searchTerm) {
    const searchTerm = filters.searchTerm.toLowerCase();
    filteredTransactions = filteredTransactions.filter(transaction =>
      transaction.description.toLowerCase().includes(searchTerm) ||
      transaction.category.toLowerCase().includes(searchTerm) ||
      transaction.tags.some(tag => tag.toLowerCase().includes(searchTerm)) ||
      transaction.cleanMerchant.cleanName.toLowerCase().includes(searchTerm)
    );
  }

  return filteredTransactions;
};

// Format transaction data for export
export const formatTransactionForExport = (
  transaction: Transaction,
  options: ExportOptions = defaultExportOptions
): Record<string, any> => {
  const formatCurrency = (amount: number): string => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: options.currencyFormat,
    }).format(amount);
  };

  const formatDate = (dateString: string): string => {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
    });
  };

  return {
    'Transaction ID': transaction.id,
    'Date': formatDate(transaction.date),
    'Description': transaction.description,
    'Amount': formatCurrency(transaction.amount),
    'Category': transaction.category,
    'Tags': transaction.tags.join(', '),
    'Account ID': transaction.accountId,
    'Merchant': transaction.cleanMerchant.cleanName,
    'Status': transaction.pending ? 'Pending' : 'Cleared',
    'Notes': transaction.notes || '',
    'Created At': formatDate(transaction.createdAt),
    'Updated At': formatDate(transaction.updatedAt),
  };
};

// Export transactions to CSV
export const exportTransactionsToCSV = (
  transactions: Transaction[],
  filters: FilterOptions,
  options: ExportOptions = defaultExportOptions
): string => {
  const filteredTransactions = filterTransactionsForExport(transactions, filters);
  
  if (filteredTransactions.length === 0) {
    throw new Error('No transactions found matching the current filters.');
  }

  const formattedData = filteredTransactions.map(transaction =>
    formatTransactionForExport(transaction, options)
  );

  const csvString = stringify(formattedData, {
    header: options.includeHeaders,
    columns: Object.keys(formattedData[0]),
  });

  return csvString;
};

// Export transactions to JSON
export const exportTransactionsToJSON = (
  transactions: Transaction[],
  filters: FilterOptions,
  options: ExportOptions = defaultExportOptions
): string => {
  const filteredTransactions = filterTransactionsForExport(transactions, filters);
  
  if (filteredTransactions.length === 0) {
    throw new Error('No transactions found matching the current filters.');
  }

  const formattedData = filteredTransactions.map(transaction =>
    formatTransactionForExport(transaction, options)
  );

  return JSON.stringify(formattedData, null, 2);
};

// Download file function
export const downloadFile = (content: string, filename: string, mimeType: string): void => {
  const blob = new Blob([content], { type: mimeType });
  const url = URL.createObjectURL(blob);
  const link = document.createElement('a');
  link.href = url;
  link.download = filename;
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
};

// Generate filename with timestamp
export const generateExportFilename = (
  prefix: string,
  format: 'csv' | 'json',
  includeTimestamp: boolean = true
): string => {
  const timestamp = includeTimestamp 
    ? `_${new Date().toISOString().slice(0, 10)}_${new Date().toTimeString().slice(0, 8).replace(/:/g, '-')}`
    : '';
  
  return `${prefix}${timestamp}.${format}`;
};

// Main export function
export const exportTransactions = (
  transactions: Transaction[],
  filters: FilterOptions,
  options: ExportOptions = defaultExportOptions
): void => {
  try {
    let content: string;
    let filename: string;
    let mimeType: string;

    if (options.format === 'csv') {
      content = exportTransactionsToCSV(transactions, filters, options);
      filename = generateExportFilename('transactions', 'csv');
      mimeType = 'text/csv';
    } else {
      content = exportTransactionsToJSON(transactions, filters, options);
      filename = generateExportFilename('transactions', 'json');
      mimeType = 'application/json';
    }

    downloadFile(content, filename, mimeType);
  } catch (error) {
    console.error('Export failed:', error);
    throw error;
  }
};

// Export summary statistics
export const exportSummaryStatistics = (
  transactions: Transaction[],
  filters: FilterOptions,
  options: ExportOptions = defaultExportOptions
): void => {
  const filteredTransactions = filterTransactionsForExport(transactions, filters);
  
  const summary = {
    'Total Transactions': filteredTransactions.length,
    'Total Income': filteredTransactions
      .filter(t => t.amount > 0)
      .reduce((sum, t) => sum + t.amount, 0),
    'Total Expenses': Math.abs(filteredTransactions
      .filter(t => t.amount < 0)
      .reduce((sum, t) => sum + t.amount, 0)),
    'Net Flow': filteredTransactions.reduce((sum, t) => sum + t.amount, 0),
    'Average Transaction': filteredTransactions.length > 0 
      ? filteredTransactions.reduce((sum, t) => sum + Math.abs(t.amount), 0) / filteredTransactions.length
      : 0,
    'Pending Transactions': filteredTransactions.filter(t => t.pending).length,
    'Cleared Transactions': filteredTransactions.filter(t => !t.pending).length,
  };

  const formatCurrency = (amount: number): string => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: options.currencyFormat,
    }).format(amount);
  };

  const formattedSummary = {
    'Total Transactions': summary['Total Transactions'],
    'Total Income': formatCurrency(summary['Total Income']),
    'Total Expenses': formatCurrency(summary['Total Expenses']),
    'Net Flow': formatCurrency(summary['Net Flow']),
    'Average Transaction': formatCurrency(summary['Average Transaction']),
    'Pending Transactions': summary['Pending Transactions'],
    'Cleared Transactions': summary['Cleared Transactions'],
  };

  let content: string;
  let filename: string;
  let mimeType: string;

  if (options.format === 'csv') {
    content = stringify([formattedSummary], {
      header: options.includeHeaders,
    });
    filename = generateExportFilename('summary_statistics', 'csv');
    mimeType = 'text/csv';
  } else {
    content = JSON.stringify(formattedSummary, null, 2);
    filename = generateExportFilename('summary_statistics', 'json');
    mimeType = 'application/json';
  }

  downloadFile(content, filename, mimeType);
}; 